@import url(https://fonts.googleapis.com/css2?family=Lilita+One&family=Montserrat:wght@400;500&family=Unica+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p,
button,
label,
select {

  font-family: 'Montserrat', sans-serif !important;

}

h1,
h2,
h3,
h4,
h5,
h6,
span {

  font-family: 'Unica One', cursive !important;

}

.MuiAppBar-colorPrimary,
.MuiButton-containedPrimary {

  background-color: #209CEE !important;

}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.loginForm {

  margin: 120px auto;
  border: 2px solid  #232b2bb4;
  width: 300px;
  padding: 20px;
  position: relative;
  border-radius: 8px;

}

.logo {

  margin-top: -60px;
  margin-bottom: -60px;

}

.loginHeader {

  margin: 10px 0;
  color: #232b2bd8;

}

#inputUsername,
#inputPassword {

  margin: 10px;
  border: 1px solid #232b2bb4;
  border-radius: 4px;
  padding-left: 5px;
  height: 20px;

}

.loginButton,
.registerNavButton {

  width: 200px;
  height: 24px;
  margin-top: 10px;
  color: #ffff;
  background-color: #209CEE;
  border: 2px solid #209CEE;
  border-radius: 3px;
  transition: all .4s ease;
  -webkit-transition: all .4s ease;
  cursor: pointer;

}

.loginButton:hover,
.registerNavButton:hover {

  background-color: #1772af;
  border: 2px solid #1772af;

}

.registerNavButton {

  margin-bottom: 30px;

}
.cartNavButton {

  width: 200px;
  height: 24px;
  margin-top: 10px;
  color: #ffff;
  background-color: #209CEE;
  border: 2px solid #209CEE;
  border-radius: 3px;
  transition: all .4s ease;
  -webkit-transition: all .4s ease;
  cursor: pointer;

}

.cartNavButton:hover {

  background-color: #1772af;
  border: 2px solid #1772af;

}
.productContainer {

  width: 90%;
  display: flex;
  justify-content: space-evenly;
  margin: 0 auto;

}

.MuiGrid-container {

  display: flex;
  margin-top: 60px !important;
  justify-items: center !important;

}

.MuiPaper-root {
  
  margin: auto !important;

}

.MuiCardMedia-root {

  background-size: contain !important;

}
.registerForm {

  margin: 120px auto;
  border: 2px solid  #232b2bb4;
  width: 300px;
  padding: 20px;
  position: relative;
  border-radius: 8px;

}

.registerButton,
.loginNavButton {

  width: 220px;
  height: 24px;
  margin-top: 10px;
  color: #ffff;
  background-color: #209CEE;
  border: 2px solid #209CEE;
  border-radius: 3px;
  transition: all .4s ease;
  -webkit-transition: all .4s ease;
  cursor: pointer;

}

.loginNavButton:hover,
.registerButton:hover {

  background-color: #1772af;
  border: 2px solid #1772af;

}

.loginNavButton {

  margin-bottom: 30px;

}

.accountTypeWrapper > label {

  display: block;
  margin: 10px 0;

}

#inputUsername,
#inputPassword,
#inputFirstName,
#inputLastName,
#inputEmail {

  margin: 10px;
  border: 1px solid #232b2bb4;
  border-radius: 4px;
  padding-left: 5px;
  height: 20px;

}
.historyComponentWrapper {

  margin: 0 auto;
  margin-top: 60px;
  display: flex;
  flex-direction: column;

}

.historyComponentWrapper > h2 {

  text-align-last: left;

}

#root > div > div > div.historyWrapper > div.historyComponentWrapper > div {

  width: 200px;

}

.historyCard,
.historyContainer,
.historyComponentWrapper {

  width: 90vw;

}

.historyCard > div {

  width: 90vw;
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;

}

.historyCardContents {

  width: 300px;
  border: 3px solid #209CEE;
  border-radius: 8px;
  margin: 20px 20px;
  padding: 10px 20px;
  text-align: left;

}

.historyCardContents > p {

  margin: 8px 0;

}

.historyIdAndTimeWrapper {

  font-size: 11px;
  display: flex;
  justify-content: space-between;

}

.MuiSelect-selectMenu {

  text-align: left;

}
.profileWrapper {

  width: 90%;
  margin: 0 auto;

}

.profileOwner {
  
  margin-top: 60px;
  margin-bottom: 60px;
  border: 3px solid #209CEE;
  border-radius: 8px;
  width: 250px;

}

.profileOwner > h1 {

  margin: 16px 0;
  text-align-last: left;
  padding-left: 10px;

}

.profileDetails > p {

  margin: 10px 0;
  text-align-last: left;
  padding-left: 10px;

}

.topBorder {

  width: 50px;
  border-top: 10px dotted #209CEE;
  margin: 0 auto;

}
